body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    text-align: center;
    display: flex;
    justify-content: center;
}

.DatePickerTemplate{
    max-width: 500px;
    width: 800px;
    background-color: lightgray;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05), 0 1px 3px 0 rgba(63, 63, 68, 0.15); 
}

.DatePickerTitle{
    background-color: white;
    font-weight: bold;
    font-size: 2em;
}

.DatePickerDayNamesContainer{
    background-color: white;
    margin-bottom: 1px;
}

.DatePickerDaysContainer{
    background-color: white;
}



.dayNamesMarkup{
    display: flex;
    justify-content: center;
}

.DayName{ 
    flex: 1 0;
    background: transparent;
    font-size: 1em;
    font-weight: 550;
    text-align: center;
    color: rgb(141, 141, 141);
    padding: 0.5em;
}


.week{
    display: flex;
    justify-content: center;
}

.Day{
    flex: 1 0;
    background: transparent;
    font-size: 1em;
    font-weight: 600;
    text-align: center;
    padding: 0.5em; 
}

.EmptyStateDay{
    display: block;
    flex: 1 0;
    width: 100%;
    outline: none;
    border: none;

    background: transparent;
}


.Day > button {
    display: block;
    flex: 1 0;
    width: 100%;
    height: 100%;
    padding-top: 0.8em;
    
    outline: none;
    border: none;
}

.Day > div > button{
    display: block;
    flex: 1 0;
    width: 100%;
    height: 100%;
    padding-top: 0.8em;
    
    outline: none;
    border: none;
}

.Selected{
    display: block;
    flex: 1 0;
    width: 100%;

    background: rgb(0, 185, 175);
    color: white;
    outline: none;
    border: none;
    cursor: pointer;
}

.MultiSelected{
    display: block;
    flex: 1 0;
    width: 100%;  

    background: rgb(73, 161, 251);
    color: white;
    outline: none;
    border: none;
    cursor: pointer;
}

.BetweenSelected{
    background: rgb(234, 249, 248);  
    cursor: default;  
}

.Gray{   
    background: transparent;
    color:  rgb(213, 213, 213);
    cursor: default;
}

.Green{
    background: transparent;
    color:  rgb(44, 199, 50);
    cursor: default;   
}

.Black{ 
    background: transparent; 
    color: black;
    cursor: pointer;   
}

.Red{ 
    background: transparent;  
    color: rgb(242, 97, 98);
    cursor: pointer;
}

.Blue{  
    background: transparent;
    color: rgb(73, 161, 251);
    cursor: pointer;
}

.ButtonLayout{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.ButtonText{
    font-size: 0.7em; 
    min-height: 2em;
}
.Popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.5);
}
.PopupInner {
    position: absolute;
    left: 25%;
    top: 25%;
    right: 25%;
    bottom: 25%;

    max-width: 500px;
    width: 300px;
    height: 100px;

    margin: auto;
    background: white;

    display: flex;
    flex-direction: column; 
    justify-content: center;
}

input{
    margin: 10px;
}

.SubmitButton{
    margin: 5px;
}

.CancelButton{
    margin: 5px;
}
